:root{
    --primary-color: #4f20a0;
    --bg-main-color: #f4f0fe;

    --white-color: #fff;
    --dark-gray: #94919c;

    --default-padding: 0 4rem;
}
.container{
    display: flex;
    flex-direction: column;
    height: 100vh;
}
.container>*{
    padding: var(--default-padding);
}
.container>div:first-child>*{
    padding: 2rem 0;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.container>div:first-child a,
.container>div:first-child a:link,
.container>div:first-child a:visited{
    position: relative;
    color: var(--primary-color);
    text-decoration: none;
    font-weight: 500;
    padding-left: 10px;
    display: flex;
    align-items: center;
    background-color: var(--white-color);
}
.container>div:first-child a::before{
    position: absolute;
    content: "\2190";
    font-size: 50px;
    width: 10px;
    left: 0;
    height: 30px;
    top: -.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: -1;
}
.container>div:last-child{
    display: flex;
    flex-direction: row;
    flex: 1;
}
.container>div:last-child>div{
    position: relative;
    flex: 1;
    max-width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container img{
    max-width: 100%;
}

form{
    background-color: var(--bg-main-color);
    padding: 2rem;
    display: flex;
    flex-direction: column;
    border-radius: 30px;
}
form>div:first-child{
    display: flex;
    flex-direction: row;
    align-items: center;
    color: #b287fb;
}
form>div:first-child h3{
    color: var(--primary-color);
}
form>div:first-child label {
    color: var(--dark-gray);
}

form>div:first-child>div{
    line-height: 8px;
    margin-bottom: 1rem;
}


form>div:last-child{
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
}
form>div:last-child button{
    width: 100%;
    padding: 1rem 1.5rem;
    text-transform: uppercase;
    font-weight: bold;
    background-color: #ded8e4;
    border: none;
    cursor: pointer;
}
form>div:last-child button:last-child{
    margin-top: 1rem;
    color: var(--primary-color);
    background-color: transparent;
    border: solid 2px #c3e1fb;
}

.cad-modal-container{
    padding: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.cad-modal-container>*{
    margin: .5rem 0;
}
.cad-modal-container span{
    text-align: center;
}

.btn{
    background-color: #1070e8;
    width: 100%;
    border: none;
    padding: 1rem 1.5rem;
    text-transform: uppercase;
    color: var(--white-color);
}

/*** Gria ***/

.gria-input{
    position: relative;
    background-color: var(--white-color);
    position: relative;
    display: flex;
    border-bottom: solid .15rem rgba(0, 0, 0, .5);
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    margin: 2rem 0;
}
.gria-input input{
    flex: 1;
    outline: none;
    padding: 1rem 1.5rem;
    border: none;
    padding-top: 1.5rem;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}
.gria-input label{
    position: absolute;
    z-index: 1;
    left: .5rem;
    transition: all .2s;
    bottom: .91rem;
    left: 1.5rem;
    color: var(--dark-gray);
    color: #000;
}
.gria-input input::placeholder{
    color: transparent;
}
.gria-input input:focus ~ label{
    opacity: 1;
    left: .5rem;
    bottom: 2rem;
    font-size: .9rem;
    color: var(--dark-gray);
}
.gria-input input[data="true"] ~ label{
    opacity: 1;
    left: .5rem;
    bottom: 2rem;
    font-size: .9rem;
    color: var(--dark-gray);
}
.gria-input input[data="true"]{
    position: relative;
}
.input-true::before{
    position: absolute;
    content: "\2713";
    color: greenyellow;
    right: 1rem;
    font-size: 25px;
    top: 10px;
    z-index: 1;
    font-weight: bold;
}


/** IssModal **/

.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 5000;
    opacity: 0;
    transition: all 0.3s;
    overflow: auto;
}

.popup>div {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100%;
}

.popup_content {
    position: relative;
    width: 400px;
    height: auto;
    background-color: #fff;
    box-shadow: 0 2rem 4rem rgba(0, 0, 0, 0.1);
    opacity: 0;
    transform: scale(0.25);
    transition: all 0.5s 0.2s;
    padding: 1rem;
    border-radius: 16px;
}

.popup_close {
    color: #000;
    position: absolute;
    right: 2.5rem;
    top: 2.5rem;
    font-size: 2rem;
    text-decoration: none;
    display: inline-block;
    transition: all 0.2s;
    line-height: 1;
    border: none;
    background-color: transparent;
    cursor: pointer;
}

.popup_close:hover {
    color: #002a64;
}
@media screen and (max-width: 600px) {
    body{
        background-color: var(--bg-main-color);
    }
    .container>div:last-child>div:first-child{
        display: none;
    }
    .container>div:last-child>div:last-child{
        max-width: none;
    }
    form>div:first-child>div{
        line-height: 24px !important;
    }
    form{
        background-color: transparent;
    }
    .container>div:first-child a, .container>div:first-child a:link, .container>div:first-child a:visited{
        background-color: var(--bg-main-color);
        left: 40px;
        color: #b287fb;
    }
    .popup_content{
        width: 70%;
    }
    form>div:last-child button:last-child{
        border: none !important;
    }
}